import styled from "styled-components";
import Description from "./descriptionDesktop";
import PhoneDesktop from "./phoneDesktop";

const StyledIntro = styled.section`
  display: flex;
  position: relative;
  overflow: hidden;

  padding-top: 254px;
  padding-bottom: 120px;
  z-index: 2;

  background: radial-gradient(
      86.14% 110.41% at 0% -9.59%,
      #7400e8 0%,
      rgba(116, 0, 232, 0) 100%
    ),
    radial-gradient(
      38.96% 50.45% at 31.8% 17.2%,
      #ff7cda 0%,
      rgba(255, 124, 218, 0) 100%
    ),
    radial-gradient(
      59.26% 118.55% at 88.65% 113.3%,
      #00aee8 0%,
      rgba(0, 174, 232, 0) 100%
    ),
    radial-gradient(
      40.16% 70% at 52.73% 45.07%,
      #00aee8 0%,
      rgba(0, 174, 232, 0) 100%
    ),
    #000f67;

  & * {
    z-index: 100;
  }
`;

const IntroInner = styled.div`
  margin: 0 8.3%;
  width: 100%;
  display: flex;
  gap: 20px;
  justify-content: space-between;

  @media (max-width: 1200px) {
    width: calc(100vw - 10%);
    margin: 0 10%;
    flex-direction: column;
    gap: 60px;
  }
`;

const IntroDesktop = ({ language }) => {
  return (
    <StyledIntro id={"intro"}>
      <IntroInner>
        <Description language={language} />
        <PhoneDesktop />
      </IntroInner>
    </StyledIntro>
  );
};

export default IntroDesktop;
